.emoji-container {
    width: 100%;
    margin: auto;
    background-color: #ddd;
    padding: 1rem;
    border: 1px solid #aaa;
    display: flex;
    align-items: center;
    cursor: pointer;
}

.emoji-container:hover {
    background-color: #aaa;
}

.emoji-clipboard {
    display: none;
    color: #666;
    margin-left: auto;
}

.emoji-container:hover .emoji-clipboard {
    display: block;
}

.emoji-container img {
    width: 30px;
    height: 30px;
}

.emoji-title {
    margin-left: 1rem;
    font-weight: 600;
    font-size: 18px;
    color: black;
}

.error-msg {
    height: 43vh;
    font-size: 1.5em;
    font-weight: 600;
    text-align: center;
    padding-top: 2rem;
}