.searchbar-container span {
    display: inline-block;
    margin-bottom: .5rem;
}

input {
    padding: 1rem;
    width: 100%;
    border-radius: 5px;
    border: 1px solid #bbb;
}

input:hover, input:focus, input:active {
    background-color: #eee;
    border: 1px solid #555;
    outline: none;
}